html {
	font-size: 18px;
	max-width: 65rem;
	margin: 0 auto;
	padding: 1rem;
	line-height: 1.5;
}

@media (max-width: 981px) and (orientation: portrait) {
	html {
		font-size: 200%;
		background-color: red;
		padding: 2rem;
	}

	nav ul li {
		font-size: 180%;
		padding-bottom: 2rem;
	}

	nav ul h1 {
		margin: 0;
	}
}

a, a:visited {
	color: black;
	text-decoration: none;
}

nav ul {
	list-style: none;
	display: inline-block;
	margin: 0;
	margin-bottom: 2.25rem;
	padding: 0;
}

nav ul li {
	display: inline-block;
	margin-right: 1rem;
}

footer {
	padding: 2rem 0 0;
	margin-top: 1rem;
}

.page404 {
	text-align: center;
}

.page404 h1{
	font-size: 4em;
}
